.container {
    display: flex;
    flex-direction: row;
    width: 90%;
}
.controlPanel {
    background-color: #24262b;
    padding: 30px;
    width: 20%;
    max-width: 150px;
    border-radius: 10px;
    box-sizing: border-box;
    border-width: 1px;
    border-style: solid;
    border-color: #777;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.NickGenerator {
    background-color: #383c44;
    padding: 30px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 80%;
}

.Colours {
    display: flex;
    height: 100px;
    align-items: center;
}

.Char {
    /* height: 50px;
    width: 30px; */
    letter-spacing: 0.3em;
    font-size: 1.2em;
}

.nameInput {
    font-size: 0.8em;
}

.nameInput input[type="text"] {
    padding: 5px;
    margin: 10px 0 0 0;
    font-size: 0.7em;
    outline: none;
    text-align: center;
}